export default {
  data() {
    return {
      userInfo: []
    }
  },
  props: {
    userId: {
      Number,
      default: 0
    },
    userType: {
      Number,
      default: 0
    }
  },
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem('CURRENT_USER'));
    window.location.href = `${WX_PHP}?c=parentcopy&a=parentAddressList&studentId=${this.userInfo.studentId}&schoolId=${this.userInfo.schoolId}`;
  }
}



